.content-container {
    padding: 15px;
    background-color: ghostwhite;
    border-radius: 10px;
    overflow: auto;
    display: flex;
}

.render-content {
    margin: 5px;
    font-weight: 400;
    font-size: .9em;
    border: solid 1px gray;
    padding: 5px;
    border-radius: 5px;
    display: flex;
    flex-direction: column;   
    cursor: pointer; 
    text-decoration: none;
    color: black;
    width: max-content;
}
.render-content span {
    font-size: 1em;
    text-transform: capitalize;

}

